@import "abstracts/functions";
@import "abstracts/vars";

.header {
	width: 100%;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 20;
	background-color: #fff;
	transition: transform 0.2s ease;

	&.scroll {

		@media(min-width: 992px) {
			transform: translateY(#{rem(-49)});
		}
	}

	&__top {
		display: none;
		align-items: center;
		justify-content: space-between;
		padding: rem(9) 0;
		gap: rem(21);

		@media(min-width: 1280px) {
			padding: rem(9) 0 rem(9) 5%;
		}

		@media(min-width: 992px) {
			display: flex;
		}
	}

	&__log {
		display: flex;
		align-items: center;
		align-self: flex-end;
		flex-shrink: 0;
		margin: 0 0 rem(5);

		@media(max-width: 991px) {
		    gap: rem(20);
			margin: 0;
			align-self: center;
		}

		@media(max-width: 744px) {
			gap: rem(14);
		}

		@media(max-width: 350px) {
			gap: rem(11);
		}
	}

	&__links {
		display: flex;
		align-items: center;
		gap: rem(27);
		margin: 0 auto;
	}

	&__top-right {
		display: flex;
		align-items: center;
		gap: rem(30);

		@media(min-width: 1280px) {
			gap: rem(70);
		}
	}

	&__body {
		display: flex;
		align-items: center;
		gap: rem(11);
		padding: rem(12) 0;
		position: relative;

		@media(min-width: 351px) {
			gap: rem(16);
		}

		@media(min-width: 745px) {
			gap: rem(24);
			padding: rem(23) 0;
		}

		@media(min-width: 992px) {
			justify-content: space-between;
			gap: rem(20);
			padding: rem(10) rem(5) rem(10) 0;
		}

		&::after {
			content: '';
			display: block;
			position: absolute;
			top: 0;
			left: 50%;
			width: 100vw;
			height: 1px;
			background-color: #b5d8fd;
			z-index: 1;
			transform: translateX(-50%);
		}

		&::before {
			content: '';
			display: block;
			position: absolute;
			bottom: 0;
			left: 50%;
			width: 100vw;
			height: 1px;
			background-color: #b5d8fd;
			z-index: 1;
			transform: translateX(-50%);
		}
	}

	@media(max-width: 991px) {
		.phone-block {

			.simple-link {
				display: none;
			}
		}
	}

	@media(max-width: 744px) {
		.phone-block__body {
			display: none;
		}
	}
}

.burger {
	display: flex;
	justify-content: center;
	align-items: center;
	width: rem(32);
	height: rem(18);
	margin: 0 0 0 rem(6);
	position: relative;

	&::after {
		content: '';
		display: block;
		position: absolute;
		top: rem(-4);
		left: 0;
		transform: translateY(-50%);
		width: 164%;
		height: 256%;
		z-index: 1;
		opacity: 0;
	}

	svg {
		width: 100%;
		height: 100%;
		transform: scale(1);
		transition: transform 0.2s ease;
	}

	img {
		position: absolute;
		top: 50%;
		left: 50%;
		z-index: 1;
		width: rem(32);
		height: rem(32);
		transform-origin: center;
		transform: translate(-52%, -40%) scale(0);
		transition: transform 0.2s ease;
	}

	&.opened {

		img {
			transform: translate(-52%, -40%) scale(1);
		}

		svg {
			transform: scale(0);
		}
	}

	@media(min-width: 992px) {
		margin: 0 0 0 rem(20);
	}

	@media(min-width: 1280px) {
		display: none;
	}

	@media(max-width: 744px) {
		width: rem(22);
		height: rem(14);
	}
}

.trigger-search {
	display: flex;
	justify-content: center;
	align-items: center;
	width: rem(32);
	height: rem(32);
	color: $colorBlueDark;

	@media(max-width: 744px) {
		width: rem(24);
		height: rem(24);
	}

	svg {
		width: 100%;
		height: 100%;
	}

	@media(min-width: 992px) {
		display: none;
	}

	&:hover {
		color: $colorBlueLight;
	}
}

.lang-block {
	display: flex;
	align-items: center;
	gap: rem(10);

	&__link {
		display: flex;
		justify-content: center;
		align-items: center;
		text-align: center;
		min-width: rem(32);
		height: rem(32);
		border-radius: rem(6);
		font-family: $secFont;
		font-size: rem(14);
		color: #b5d8fd;
		transition: background-color 0.2s ease, color 0.2s ease;

		&.is-current {
			background-color: #b5d8fd;
			color: $colorBlueDark;
			cursor: default;
		}

		&[href] {
			&:hover {
				background-color: #b5d8fd;
				color: $colorBlueDark;
			}
		}
	}
}

.link-icon {
	display: flex;
	align-items: center;
	gap: rem(4);

	&__icon {
		width: rem(20);
		height: rem(20);

		@media(min-width: 1820px) {
			width: rem(28);
			height: rem(28);
		}

		img {
			width: 100%;
			height: 100%;
		}
	}

	&__text {
		font-family: $secFont;
		font-size: rem(12);
		line-height: 1;
		position: relative;

		@media(min-width: 1280px) {
			font-size: rem(14);
		}

		&::after {
			content: '';
			position: absolute;
			top: calc(100% + #{rem(2)});
			left: 0;
			width: 100%;
			height: rem(2);
			z-index: 1;
			transform: scaleX(0);
			transform-origin: left;
			transition: transform 0.2s ease;
		}

		.link-icon--red & {
			color: #fe9382;

			&::after {
				background-color: #fe9382;
			}
		}

		.link-icon--yellow & {
			color: #f3d144;

			&::after {
				background-color: #f3d144;
			}
		}

		.link-icon--black & {
			color: #000;

			&::after {
				background-color: #000;
			}
		}

		&:hover {

			&::after {
				transform: scaleX(1);
			}
		}
	}
}

.logo {
	display: block;
	flex-shrink: 0;

	&--header {
		width: rem(180);
		height: rem(50);

		@media(min-width: 1200px) {
			width: rem(204);
			height: rem(50);
		}

		@media(max-width: 991px) {
			width: rem(134);
			height: rem(34);
			margin: 0 auto 0 0;
		}

		@media(max-width: 744px) {
			width: rem(106);
			height: rem(26);
		}
	}

	&--footer {
		width: rem(126);
		height: rem(22);
		margin: 0 auto rem(19);
	}

	&--footer-mob {
		width: rem(90);
		height: rem(22);
		margin: 0 auto rem(10);
		display: none;

		@media(max-width: 639px) {
		    display: flex;
		    justify-content: center;
		    align-items: center;
			order: 10;
		}
	}

	img {
		width: 100%;
		height: 100%;
	}
}

.search-block {
	width: 0;
	max-width: rem(420);
	position: relative;

	@media(min-width: 992px) {
		width: 100%;
	}

	@media(min-width: 1280px) {
		margin: 0 0 0 rem(-7);
	}

	&__body {
		position: relative;
		width: 100%;
		display: none;

		@media(min-width: 992px) {
			display: block;
		}

	}

	&__line {
		position: absolute;
		top: calc(100% - 1px);
		left: 0;
		z-index: 1;
		width: 100%;
		height: 1px;
		background-color: $colorBlueDark;
		transform: scaleX(0);
		transform-origin: left;
		transition: transform 0.2s ease;
	}

	&__icon {
		display: flex;
		justify-content: center;
		align-items: center;
		width: rem(24);
		height: rem(24);
		position: absolute;
		top: 50%;
		left: 0;
		z-index: 1;
		transform: translateY(-50%);

		img {
			width: 100%;
			height: 100%;
		}
	}

	&__close {
		display: flex;
		justify-content: center;
		align-items: center;
		position: absolute;
		top: 50%;
		right: rem(4);
		z-index: 1;
		transform: translateY(-50%);
		width: rem(23);
		height: rem(23);
		background-color: #f0f6fd;
		color: $colorBlueDark;
		border-radius: 100%;
		transition: background-color 0.2s ease, opacity 0.2s ease;
		opacity: 0;
		pointer-events: none;

		&::after {
			content: '';
			display: block;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 180%;
			height: 180%;
			z-index: 1;
			opacity: 0;
		}

		&.visible {
			opacity: 1;
			pointer-events: all;
		}

		svg {
			width: 40%;
			height: 40%;
		}

		&:hover {
			background-color: $colorBlueDark;
			color: #ffffff;
		}
	}

	&__input {
		width: 100%;
		height: rem(40);
		font-family: $mainFont;
		font-size: rem(14);
		color: $colorBlueDark;
		padding: 0 rem(48) 0 rem(29);
		pointer-events: all;

		@media(min-width: 992px) {
			font-size: rem(12);
			border-bottom: 1px solid #b5d8fd;
			padding: 0 rem(32) 0 rem(40);
		}

		&::placeholder {
			font-family: $mainFont;
			font-size: inherit;
			font-weight: 500;
			color: #404040;
			transition: opacity 0.2s ease;
		}

		&::-webkit-search-cancel-button {
			-webkit-appearance: none;
			appearance: none;
		}

		&:focus {
			&::placeholder {
				opacity: 0;
			}

			~ .search-block__line {

				@media(min-width: 992px) {
					transform: scaleX(1);
				}
			}
		}

		&:hover:not(:focus) {
			~ .search-block__line {
				@media(min-width: 992px) {
					transform: scaleX(0.065);
				}
			}
		}
	}
}

.search-dropdown {
	width: 100%;
	position: absolute;
	top: calc(100% - 1px);
	left: 50%;
	z-index: 1;
	opacity: 0;
	visibility: hidden;

	&.show {
		opacity: 1;
		visibility: visible;
		transform: translateY(0);

		@media(min-width: 992px) {
			transform: translateX(-50%) translate(0, 0);
		}
	}

	@media(min-width: 992px) {
	    min-width: rem(400);
		transform: translate(0, 30px) translateX(-50%);
		transition: transform 0.5s ease, opacity 0.5s ease, visibility 0.5s ease;
	}

	@media(max-width: 991px) {
	    position: fixed;
		width: 100%;
		top: rem(80);
		left: 0;
		z-index: 20;
		height: calc(100% - 80px);
		background-color: #fff;
		transform: translateY(5%);
		transition: transform 0.2s cubic-bezier(0.0, 0.0, 0.2, 1),  opacity 0.2s ease, visibility 0.2s ease;
	}

	@media(max-width: 744px) {
		top: rem(50);
		height: calc(100% - 50px);
	}

	&__body {
		display: flex;
		flex-direction: column;
		padding: 0;
		background-color: #fff;
		box-shadow: 0 rem(4) rem(10) rgba(#000, 0.25);
		position: relative;

		@media(max-width: 991px) {
			padding: 0 rem(20);
			height: 100%;
			box-shadow: none;
		}
	}

	&__top {
		font-family: $secFont;
		font-size: rem(18);
		color: $colorBlueDark;
		text-align: center;
		padding: rem(27) 0 rem(14);
		margin: 0 0 rem(16);

		@media(min-width: 992px) {
			display: none;
		}
	}

	&__close {
		width: rem(24);
		height: rem(24);
		position: absolute;
		top: rem(26);
		right: rem(24);
		z-index: 1;

		&::after {
			content: '';
			display: block;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 218%;
			height: 234%;
			z-index: 1;
			opacity: 0;
		}

		img {
			width: 100%;
			height: 100%;
		}

		@media(min-width: 992px) {
			display: none;
		}
	}

	.search-block__body {
		display: none;

		@media(max-width: 991px) {
			display: flex;
		}
	}

	&__bottom {
		border-top: 1px solid #dbebfc;
	}

	&__list {
		overflow: auto;
		width: calc(100% + 10px);
		padding: 0 rem(10) 0 0;

		@media(max-width: 991px) {
			height: calc(100% - 60px);
		}

		@media(min-width: 992px) {
			max-height: rem(520);
		}

		&::-webkit-scrollbar-track
		{
			border-radius: 20px;
			background-color: #fff;
			width: 8px;
			border: 1px solid #dbebfc;
		}

		&::-webkit-scrollbar
		{
			width: 8px;
			background-color: #fff;
			border: 1px solid #dbebfc;
		}

		&::-webkit-scrollbar-thumb
		{
			border-radius: 20px;
			width: 2px;
			background-color: #dbebfc;
		}
	}

	&__result {
		padding: rem(10) rem(20) rem(44);
		display: none;

		&.has-result {
			display: block;
		}

		@media(max-width: 991px) {
			padding: 0;
			margin: rem(10) 0 0;
			height: calc(100% - 115px);
		}
	}

}

.empty-search {
	padding: rem(16) 0 rem(41);
	margin: rem(12) 0 0;

	@media(min-width: 992px) {
		padding: rem(25) rem(20) rem(41);
		margin: 0;
	}

	@media(max-width: 991px) {
	    border-top: 1px solid #d6e6fd;
	}


	&__error {
		font-size: rem(14);
		line-height: 1.2;
		color: #ab0719;
		font-weight: 400;
		text-align: center;
		max-width: rem(300);
		margin: 0 auto rem(13);
		display: none;

		&.show {
			display: block;
		}
	}

	&__title {
		font-size: rem(16);
		font-weight: bold;
		color: $colorBlueDark;
		text-align: center;
	}

	&__sub-title {
		font-family: $secFont;
		font-size: rem(16);
		color: $colorBlueDark;
	}

	&__bottom {
		margin: rem(52) 0 0;
	}
}

.srch-link {
	display: flex;
	align-items: center;
	color: $colorBlueDark;
	font-size: rem(18);
	gap: rem(6);
	font-weight: 400;
	line-height: 1;
	padding: rem(12) 0 rem(12) rem(20);
	border-bottom: 1px solid #dbebfc;

	@media(max-width: 639px) {
		padding: rem(13) 0;
		border-bottom: 1px solid rgba(#fff, 0.1);
	}

	&__icon {
		display: flex;
		justify-content: center;
		align-items: center;
		width: rem(34);
		height: rem(14);
		position: relative;
		overflow: hidden;
		flex-shrink: 0;

		svg {
			position: absolute;
			top: 50%;
			left: -11%;
			z-index: 1;
			transform: translate(-50%, -50%);
			width: rem(32);
			height: rem(10);
			transition: left 0.2s ease;
		}
	}

	&__text {
		transform: translateX(-17px);
		transition: transform 0.2s ease;
	}

	&:hover {
		color: $colorBlueLight;

		svg {
			left: 50%;
		}

		.srch-link__text {
			transform: translateX(5px);
		}
	}
}

.search-cart {
	display: flex;
	align-items: center;
	gap: rem(12);
	padding: rem(9) 0;
	border-bottom: 1px solid #dbebfc;

	&:last-child {
		border: none;
	}

	@media(max-width: 991px) {
		padding: rem(10) 0;

		&:first-child {
			border-top: 1px solid #dbebfc;
		}
	}

	&__img {
		display: block;
		width: rem(80);
		height: rem(100);
		flex-shrink: 0;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__text {
		font-size: rem(14);
		line-height: 1.2;
		color: $colorBlueDark;
		display: -webkit-box;
		-webkit-line-clamp: 4;
		-webkit-box-orient: vertical;
		overflow: hidden;

		&:hover {
			color: $colorBlueLight;
		}
	}
}

.search-all {
	display: flex;
	align-items: center;
	justify-content: flex-end;
	gap: rem(10);
	color: #4abbf3;
	margin: rem(12) rem(-2) 0 auto;

	&__icon {
		display: flex;
		justify-content: center;
		align-items: center;
		width: rem(16);
		height: rem(10);
	}

	&__text {
		font-size: rem(14);
		line-height: 1;
		position: relative;

		&::after {
			content: '';
			position: absolute;
			top: calc(100% + #{rem(2)});
			left: 0;
			width: 100%;
			height: rem(1);
			z-index: 1;
			transform: scaleX(0);
			transform-origin: left;
			transition: transform 0.2s ease;
			background-color: $colorBlueDark;
		}
	}

	&:hover {
		color: $colorBlueDark;

		.search-all__text {
			&::after {
				transform: scaleX(1);
			}
		}
	}
}
